@import url("https://fonts.googleapis.com/css?family=Open+Sans&display=swap");

.proyectos {
  margin-bottom: 3rem;
}
.contenedor-proyectos {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 1rem;
  grid-row-gap: 2rem;
  width: 80%;
}

.HTML5 {
  color: #ff8741;
}
.CSS3 {
  color: #5cbbfa;
}
.JavaScript {
  color: #eed65c;
}
.Bootstrap {
  color: #ad84e2;
}
.ReactJS {
  color: #60e96c;
}
.TypeScript {
  color: rgb(112, 112, 212);
}
.Angular {
  color: rgb(241, 123, 123);
}
.Material {
  color: rgb(170, 230, 215);
}

.proyecto-card {
  width: 100%;
  height: 25em;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-top-left-radius: 30px;
  border-bottom-right-radius: 30px;
  background: transparent;
  overflow: hidden;
  border: none;
  box-shadow: 5px 5px 6px rgb(0 0 0 / 45%);
}

.proyecto-card .proyecto-img {
  height: 100%;
  box-sizing: border-box;
}

.proyecto-card .proyecto-img img {
  display: block;
  width: 100%;
  height: 100%;
}

.proyecto-descrip {
  position: absolute;
  width: 100%;
  bottom: -75%;
  background: #4967bb;
  background: linear-gradient(271deg, #193171, #0f0f39);
  color: white;
  padding: 10px;
  box-sizing: border-box;
  transition: 0.5s;
  height: 85%;
}

.proyecto-card:hover .proyecto-descrip {
  bottom: 0;
}

.proyecto-descrip h2 {
  margin: 0;
  padding: 0;
  font-size: 20px;
  width: 100%;
  font-style: italic;
}

.proyecto-descrip h2 span {
  font-size: 13px;
  color: rgba(255, 255, 255, 0.5);
  font-weight: normal;
}

label {
  display: block;
  margin-top: 5px;
  font-weight: bold;
  font-size: 15px;
  font-style: italic;
}

.lista-proyectos {
  display: grid;
  text-align: center;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 0.2rem;
}

.lenguajes {
  font-size: 12px;
}

.btn {
  display: block;
  padding: 5px;
  color: #fff;
  margin: 10px auto 0;
  width: 50%;
  font-size: 13px;
  border-radius: 50px;
  background-color: #007bff;
  border-color: #007bff;
}

.btn:hover{
  color: #00ACED;
  background-color: #ffffff;
  border-color: #00aced;
  box-shadow: 0 0 24px 0 #00aced;
  font-weight: bold;
}
@media (max-width: 990px) {
  .contenedor-proyectos {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 760px) {
  .contenedor-proyectos {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 490px) {
  .contenedor-proyectos {
    grid-template-columns: repeat(1, 1fr);
  }
}
