@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background-color: white;
}

::-webkit-scrollbar-thumb {
  background: linear-gradient(0deg, #193171, #0f0f39);
  border-radius: 5px;
}

body {
  font-family: "Oswald", sans-serif;
  background: url("https://i.pinimg.com/originals/aa/fc/cf/aafccf99c735fba775db412afeba271d.jpg");
  background-attachment: fixed;
  color: #ffffff;
}

/* NavBar */

.social-buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  margin-top: -10px;
  margin-bottom: 2%;
}
.social-buttons__button {
  margin: 10px 5px 0;
}

.social-button {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  outline: none;
  width: 40px;
  height: 40px;
  text-decoration: none;
}
.social-button__inner {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  height: 100%;
  border-radius: 100%;
  background: #fff;
  text-align: center;
  box-shadow: 4px 5px 6px rgb(0 0 0 / 74%);
}
.social-button i,
.social-button svg {
  position: relative;
  z-index: 1;
  transition: 0.3s;
}
.social-button i {
  font-size: 28px;
}
.social-button svg {
  height: 60%;
  width: 60%;
}
.social-button::after {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  display: block;
  width: 0;
  height: 0;
  border-radius: 100%;
  transition: 0.3s;
}
.social-button:focus, .social-button:hover {
  color: #fff;
}
.social-button:focus::after, .social-button:hover::after {
  width: 100%;
  height: 100%;
  margin-left: -50%;
}

.social-button--about {
  color: #01589b;
}
.social-button--about::after {
  background: #01589b;
}

.social-button--proyecto {
  color: #ed1c6f;
}
.social-button--proyecto::after {
  background: #ed1c6f;
}

.social-button--skill {
  color: #867b00;
}
.social-button--skill::after {
  background: #867b00;
}

.social-button--contact {
  color: #339406;
}
.social-button--contact::after {
  background: #339406;
}




