/* Header */
header {
  background: url("https://www.lefthudson.com/wp-content/uploads/2019/11/dark-abstract-background-unique-abstract-polygonal-space-low-poly-dark-background-motion-background-storyblocks-video-this-month-of-dark-abstract-background.png");
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
  text-align: center;
  width: 100%;
  height: calc(100vh - 150px);
  background-size: cover;
  background-attachment: fixed;
  position: relative;
  overflow: hidden;
  border-radius: 0 0 85% 85% / 30%;
}
header .overlay {
  width: 100%;
  height: 100%;
  padding: 10rem 0 0 0;
  color: #fff;
  text-shadow: 1px 1px 1px #333;
  background-image: linear-gradient(
    257deg,
    rgb(152 180 255 / 44%) 10%,
    rgb(0 34 74 / 88%) 100%
  );
}

header .overlay .tituloH h1 {
  font-size: calc(2.8em + 2.6vw);
  font-weight: 700;
  letter-spacing: 0.01em;
  text-shadow: 0.05em 0.05em 0.022em rgb(0, 0, 0);
  color: #fff;
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
}

header .overlay .tituloH h3 {
  font-family: "Open Sans", sans-serif;
  margin-bottom: 30px;
  text-shadow: 0.09em 0.09em 0.022em rgb(0, 0, 0);
  color: white;
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
}

.fade-in {
  -webkit-animation: fadeIn 4s;
          animation: fadeIn 4s;
}
@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  30% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  30% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

.overlay .tituloH:hover {
  text-shadow: 0.1em 0.1em 0.1em #111;
  -webkit-animation: shockwaveJump 1s ease-out;
  animation: shockwaveJump 1s ease-out;
  -webkit-animation-iteration-count: 2;
          animation-iteration-count: 2;
}

@-webkit-keyframes shockwaveJump {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  40% {
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
  }
  50% {
    -webkit-transform: scale(0.98);
            transform: scale(0.98);
  }
  55% {
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
  }
  60% {
    -webkit-transform: scale(0.98);
            transform: scale(0.98);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes shockwaveJump {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  40% {
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
  }
  50% {
    -webkit-transform: scale(0.98);
            transform: scale(0.98);
  }
  55% {
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
  }
  60% {
    -webkit-transform: scale(0.98);
            transform: scale(0.98);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

