
.footer-bottom {
    background-color: #343a40;
}

.footer-bottom {
    padding: 15px 0;
    border-top: 1px solid #343a40 ;
    background-color: #343a40 !important;
    color: #ffffff;
    font-family: rubik;
}

.inline-inside {
    font-size: 0;
    line-height: 0;
}
.clearfix:after, .clearfix:before {
    content: "";
    display: table;
}


#footer-socials {
    text-align: right;
}

#footer-socials .socials {
    text-align: right;
    margin: 0 -7px;
    display: inline-block;
    vertical-align: middle;
}

.socials-item {
    display: inline-block;
    vertical-align: top;
    text-align: center;
    -o-transition: all 0.3s;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    margin: 0 5px;
    height: 1em;
    width: 1em;
    line-height: 16px;
    padding: 10px;
    border-radius: 50%;
    background-color: #000000;
    border: 1px solid #ffffff;
    box-shadow: 3px 9px 16px rgba(93,93,93 , 0.4), -3px -3px 10px rgba(110, 110, 110, 0.06), inset 14px 14px 26px rgba(255, 255, 255, 0.3), inset -3px -3px 15px rgba(112, 112, 112, 0.05);
}

.socials-item .icono {
    display: inline-block;
    
    width: 0.5em;
    height: 0.5em;
    line-height: 1;
    font-size: 16px;
    text-align: center;
    vertical-align: top;
    
}

.facebook {
    color: #4e64b5;
}

.twitter {
    color: #00aced;
}
.instagram {
    color: #9a8f62;
}
.youtube {
    color: #c82929;
}

.telegram {
    color: #2ca5e0;
}


.socials-item:hover {
    box-shadow: 0 0px 20px rgba(84, 1, 74, 0.7);
    border-color: rgba(255, 6, 224, 0.61);
    background: linear-gradient(to right, rgba(255, 9, 9, 0.12941176470588237), #c000ffb5, rgba(255, 0, 94, 0.14));
}

.footer-bottom a:hover {
    color: white;
}

.prueba{
    animation-name: crecer;
    animation-duration: 3s;
  }
  
  @keyframes crecer {
    from {transform: scale(0);}
    to {transform: scale(1);}
  }

  .toggled{
     
    animation-name: achicar;
    animation-duration: 3s;
    visibility: hidden;    
   
  }

  @keyframes achicar {
    from {transform: scale(1);}
    to {transform: scale(0);}
  }
