.conocimientos{
    justify-content: center;
    text-align: center;
    align-items: center;
    align-self: center;
    margin-bottom: 2rem;
  }
  .contenedor-skill {
    display: grid;
    justify-content: center;
    text-align: center;
    align-items: center;
    align-self: center;
    grid-column-gap: 2rem;
    grid-row-gap: 2rem;
    margin-bottom: 2%;
    grid-template-columns: repeat(3, 1fr);
    margin: auto 10%;
    width: 80%;
  }
  
  .flip-card {
    background-color: transparent;
    width: 100%;
    height: 200px;
    border: 1px solid #f1f1f1;
    perspective: 1000px;
  }
  
  .flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;
  }
  
  .flip-card:hover .flip-card-inner {
    transform: rotateY(180deg);
  }
  
  .flip-card-front,
  .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
  }
  
  .flip-card-front {
    background-color: rgb(255, 255, 255);
    color: black;
  }
  
  .flip-card-back {
    background: rgb(25,49,113);
    background: linear-gradient(121deg, rgba(25,49,113,1) 48%, rgba(15,15,57,1) 82%);
    color: white;
    transform: rotateY(180deg);
  }
  
  .link {
    text-decoration: none;
    color: rgb(255, 255, 255);
    cursor: pointer;
  }
  
  .link {
    position: relative;
    transition: -webkit-clip-path 275ms ease;
    transition: clip-path 275ms ease;
    transition: clip-path 275ms ease, -webkit-clip-path 275ms ease;
  }
  .link:hover .h3FlipCard::before, .link:focus .h3FlipCard::before {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
            clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
  .link .h3FlipCard {
    position: relative;
    display: inline-block;
    color: #ffffff;
  }
  .link .h3FlipCard::before {
    position: absolute;
    content: attr(data-content);
    color: rgb(37, 6, 175);
    text-decoration: underline;
    -webkit-text-decoration-color: rgb(37, 6, 175);
            text-decoration-color: rgb(37, 6, 175);
    -webkit-clip-path: polygon(0 0, 0 0, 0% 100%, 0 100%);
            clip-path: polygon(0 0, 0 0, 0% 100%, 0 100%);
    transition: -webkit-clip-path 275ms ease;
    transition: clip-path 275ms ease;
    transition: clip-path 275ms ease, -webkit-clip-path 275ms ease;
  }


   @media (max-width:830px){
    .contenedor-skill {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  
  @media (max-width:530px){
    .contenedor-skill {
      grid-template-columns: repeat(1, 1fr);
    }
  }
