/* Sobre Mi */

.sobreMi {
    min-height: 80vh;
    color: rgb(255, 255, 255);
    margin-bottom: 2%;
    text-align: justify;
  }

.titulo {
    margin-bottom: 2%;
    text-align: center;
    font-size: 3rem;
    font-family: Concert One;
    font-weight: 500;
  } 

  .texto {
    padding: 1.5rem 0;
    background: #101010;
    color: red;
    font-family: monospace, Arial;
    width: 80%;
    min-height: 27.5rem;
    margin: 0 auto;
    margin-bottom: 2%;
    border-radius: 0.625rem;
  }
  
  .direccion {
    margin-bottom: 1rem;
    width: 90%;
    margin: 0 auto;
  }
  
  .descripcion {
    color: grey;
    font-weight: 100;
    line-height: 1.5;
    width: 90%;
    margin: 0 auto;
  }
  