/* Header */
header {
  background: url("https://www.lefthudson.com/wp-content/uploads/2019/11/dark-abstract-background-unique-abstract-polygonal-space-low-poly-dark-background-motion-background-storyblocks-video-this-month-of-dark-abstract-background.png");
  transform: scaleX(-1);
  text-align: center;
  width: 100%;
  height: calc(100vh - 150px);
  background-size: cover;
  background-attachment: fixed;
  position: relative;
  overflow: hidden;
  border-radius: 0 0 85% 85% / 30%;
}
header .overlay {
  width: 100%;
  height: 100%;
  padding: 10rem 0 0 0;
  color: #fff;
  text-shadow: 1px 1px 1px #333;
  background-image: linear-gradient(
    257deg,
    rgb(152 180 255 / 44%) 10%,
    rgb(0 34 74 / 88%) 100%
  );
}

header .overlay .tituloH h1 {
  font-size: calc(2.8em + 2.6vw);
  font-weight: 700;
  letter-spacing: 0.01em;
  text-shadow: 0.05em 0.05em 0.022em rgb(0, 0, 0);
  color: #fff;
  transform: scaleX(-1);
}

header .overlay .tituloH h3 {
  font-family: "Open Sans", sans-serif;
  margin-bottom: 30px;
  text-shadow: 0.09em 0.09em 0.022em rgb(0, 0, 0);
  color: white;
  transform: scaleX(-1);
}

.fade-in {
  animation: fadeIn 4s;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  30% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

.overlay .tituloH:hover {
  text-shadow: 0.1em 0.1em 0.1em #111;
  -webkit-animation: shockwaveJump 1s ease-out;
  animation: shockwaveJump 1s ease-out;
  animation-iteration-count: 2;
}

@-webkit-keyframes shockwaveJump {
  0% {
    transform: scale(1);
  }
  40% {
    transform: scale(1.08);
  }
  50% {
    transform: scale(0.98);
  }
  55% {
    transform: scale(1.02);
  }
  60% {
    transform: scale(0.98);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes shockwaveJump {
  0% {
    transform: scale(1);
  }
  40% {
    transform: scale(1.08);
  }
  50% {
    transform: scale(0.98);
  }
  55% {
    transform: scale(1.02);
  }
  60% {
    transform: scale(0.98);
  }
  100% {
    transform: scale(1);
  }
}

@media (max-width: 770px) {
  header .overlay .tituloH h1 {
    font-size: 58px;
  }
  header .overlay .tituloH h3 {
    font-size: 28px;
  }

  header{
    height: calc(100vh - 200px);
  }
  header .overlay{
    padding-top: 12rem;
  }
}

@media (max-width: 500px) {
  
}

@media (max-width: 400px) {
  header .overlay .tituloH h1 {
    font-size: 45px;
  }
  header .overlay .tituloH h3 {
    font-size: 16px;
  }
  header{
    height: calc(100vh - 300px);
  }
  header .overlay{
    padding-top: 10rem;
  }
}

